<template>

    <div class="page">
        <div class="box">
            <div class="left">
                <el-upload class="upload-demo" :http-request='uploadImg' action="" :on-preview="handlePreview" :on-remove="handleRemove" :file-list="fileList" drag>
                    <i class="el-icon-upload"></i>
                    <div class="el-upload__text">(jpg/png500kb)
                        <em></em>
                    </div>
                    <div class="el-upload__tip" slot="tip"></div>
                </el-upload>
            </div>
            <div class="right"><img ref="Origin" :src="originImg" alt=""></div>
            <div class="right"><img :src="fixedImg" alt=""></div>
        </div>

        <div class="block"><br><br>
            <span class="demonstration"></span>
            <el-slider v-model="qualityImg" :format-tooltip="formatTooltip"></el-slider>
        </div>
    </div>

</template>
<script>
import EXIF from 'exif-js';
import { FixImg } from '../util/index.js'
export default {
    data () {
        return {
            qualityImg: 95,
            originImg: null,
            fixedImg: null,
            fileList: [
                {
                    name: 'Landscape_1.jpg',
                    url: 'media/Landscape_1.jpg'
                },
                {
                    name: 'Landscape_2.jpg',
                    url: 'media/Landscape_2.jpg'
                },
                {
                    name: 'Landscape_3.jpg',
                    url: 'media/Landscape_3.jpg'
                },
                {
                    name: 'Landscape_4.jpg',
                    url: 'media/Landscape_4.jpg'
                },
                {
                    name: 'Landscape_5.jpg',
                    url: 'media/Landscape_5.jpg'
                },
                {
                    name: 'Landscape_6.jpg',
                    url: 'media/Landscape_6.jpg'
                },
                {
                    name: 'Landscape_7.jpg',
                    url: 'media/Landscape_7.jpg'
                },
                {
                    name: 'Landscape_8.jpg',
                    url: 'media/Landscape_8.jpg'
                },

            ]

        }
    },
    methods: {
        uploadImg () {
            console.log('uploadImg')
        },
        formatTooltip (val) {
            return val / 100
            console.log('formatTooltip')
        },
        handleRemove () {
            this.originImg = null
            this.fixedImg = null
        },
        handlePreview (file) {
            let image = new Image(), vm = this;
            vm.originImg = image.src = file.url;
            image.onload = () => {
                FixImg(image, img => {
                    vm.fixedImg = img;
                }, vm.qualityImg / 100)
            }
        }
    },
}
</script>
<style lang="scss" scoped>
$ink: #283149;
$blue: #404b69;
$red: #f73859;
$gray: #dadada;
$snow: #f2f2f2;
img {
  display: block;
  width: 100%;
  height: auto;
}
.box {
  width: 400px;
  //   height: 200px;
  margin: 0 auto;
  display: flex;
  flex-flow: row wrap;
  > div {
    height: auto;
    border: 1px dashed $snow;
    background-color: $snow;
    flex-grow: 1;
  }
  .right {
    width: 100%;
    border-top: 1px dashed #409eff;
    padding: 0 30px;
    line-height: 30px;
  }
}
.block {
  width: 400px;
  margin: 0 auto;
}
.quality,
.selector,
.handler {
  width: 400px;
  height: 30px;
  line-height: 30px;
  margin: 10px auto;
  border-bottom: 1px dashed $gray;
}
</style>
